$(document).ready(function () {
  flashSaleCountdownInit();
  adjustProductAmount();
  ZoomImg();
  $(".product .toggle-buy").click(function (e) {
    e.preventDefault();
    $(this).closest(".product").toggleClass("show");
  });

  $(".product-more-info .view-more ").click(function (e) {
    e.preventDefault();
    $(this).prev().toggleClass("show");
    $(this).toggleClass("view-less");
  });

  if($('.user-sidemenu').length > 0) {
    $('.breadcrumb-wrapper').remove()
  }

  $("[type='radio']").addClass('uk-radio')
});

const menuMobileMapping = new MappingListener({
  selector: ".menu-wrapper",
  mobileWrapper: ".dropdown-menu .uk-dropdown",
  mobileMethod: "appendTo",
  desktopWrapper: ".dropdown-menu-wrapper",
  desktopMethod: "insertBefore",
  breakpoint: 1024,
}).watch();

function flashSaleCountdownInit() {
  $(".flash-countdown").each(function () {
    var date = $(this).data("date");
    var formatDate = moment(date, "DD-MM-YYYY").format("MM/DD/YYYY");
    $(this).countdown(formatDate, function (event) {
      $(this).html(event.strftime("%-D ng√†y %H:%M:%S"));
    });
  });
}

function adjustProductAmount() {
  $(".btn-dec").click(function (e) {
    e.preventDefault();
    let amountInput = $(this).next();
    let amountVal = parseInt(amountInput.val());
    if (amountVal <= 0) {
      amountVal = 0;
    } else {
      amountVal--;
    }
    amountInput.val(amountVal);
    amountInput.trigger('change')
  });

  $(".btn-inc").click(function (e) {
    e.preventDefault();
    let amountInput = $(this).prev();
    let amountVal = parseInt(amountInput.val());
    amountVal++;
    amountInput.val(amountVal);
    amountInput.trigger('change')
  });

  $(".quantity .minus").click(function (e) {
    e.preventDefault();
    let amountInput = $(this).next();
    let amountVal = parseInt(amountInput.val());
    if (amountVal <= 0) {
      amountVal = 0;
    } else {
      amountVal--;
    }
    amountInput.val(amountVal);
    amountInput.trigger("change");
  });

  $(".quantity .plus").click(function (e) {
    e.preventDefault();
    let amountInput = $(this).prev();
    let amountVal = parseInt(amountInput.val());
    amountVal++;
    amountInput.val(amountVal);
    amountInput.trigger("change");
  });
}

var productThumbnail = new Swiper(
  ".product-detail .product-thumbnail .swiper-container",
  {
    spaceBetween: 10,
    slidesPerView: 4,
    observer: true,
    observeParents: true,
    slideToClickedSlide: true,
    navigation: {
      prevEl: ".product-detail .product-thumbnail .swiper-prev",
      nextEl: ".product-detail .product-thumbnail .swiper-next",
    },
    breakpoints: {
      576: {
        slidesPerView: 3,
      },
    },
  }
);
var productImage = new Swiper(
  ".product-detail .product-image .swiper-container",
  {
    spaceBetween: 10,
    observer: true,
    observeParents: true,
    thumbs: {
      swiper: productThumbnail,
	},
	on: {
		slideChangeTransitionEnd: function () {
			ZoomImg()
		},
	  },
  }
);

var yearDot = new Swiper('.year-dot-slider .swiper-container', {
  spaceBetween: 20,
  slidesPerView: 9,
  observer: true,
  observeParents: true,
  slideToClickedSlide: true,
  navigation: {
    prevEl: '.year-dot-slider .swiper-prev',
    nextEl: '.year-dot-slider .swiper-next'
  },
  breakpoints: {
    768: {
      slidesPerView: 7
    },
    576: {
      slidesPerView: 5
    }
  }
})

var yearContent = new Swiper('.year-content-slider .swiper-container', {
  spaceBetween: 20,
  observer: true,
  observeParents: true,
  slideToClickedSlide: true,
  thumbs: {
    swiper: yearDot,
  },
})

function ZoomImg() {
  if($(window).width() > 1024) {
    $(".swiper-slide-active .zoom-img").ezPlus({
      gallery: "gallery_01",
      cursor: "pointer",
      galleryActiveClass: "active",
      imageCrossfade: true,
      loadingIcon: "http://www.elevateweb.co.uk/spinner.gif",
      zoomWindowWidth: "500",
      zoomWindowHeight: "500",
      borderSize: "1",
      borderColour: "#d8070d",
    });
  }
}
